section.usa-hero {
  background-image: none;
  padding-top: 0;
  padding-bottom: 0;
  .field--name-label.field--label-hidden {
    display: none;
  }
  .hero-slider-background {
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
    .field--name-field-title {
      margin-bottom: 0;
   }
  }
  .slick__slide {
    height: 400px;
    .grid-container {
      padding-top: 2rem;
      padding-bottom: 2rem;
    }
  }
  .usa-button--accent-cool, .usa-button--accent-cool:visited {
    color: #1b1b1b !important;
  }
}

